/*******************
 02 TYPOGRAPHY START
********************/
body {
  font-size: 14px;
  font-family: "Outfit", sans-serif;
  background-color: var(--bs-bodybg-color);
}

a {
  text-decoration: none;
  color: currentColor;
}
a:hover {
  color: currentColor;
}

h1, h2, h3, h4, h5, h6, p {
  margin-bottom: 0;
}

ol, ul, dl {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

h1 {
  font-size: 34px;
}

h2 {
  font-size: 30px;
}

h3 {
  font-size: 24px;
}

h4 {
  font-size: 20px;
}

h5 {
  font-size: 18px;
}

h6 {
  font-size: 16px;
  font-weight: 400;
}

p {
  color: #8392a5;
}

