}
.ckmain .ck.ck-editor__editable:not(.ck-editor__nested-editable).ck-focused {
  outline: none;
  border: none;
}

/*social list */
ul.codex-soclist {
  display: flex;
  align-items: center;
  justify-content: center;
}
ul.codex-soclist li {
  transition: all 0.5s ease;
}
ul.codex-soclist li a {
  color: #6d64ac;
  width: 32px;
  height: 32px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(109, 100, 172, 0.1);
  transition: all 0.5s ease;
}
ul.codex-soclist li:nth-child(n+2) {
  margin-left: 10px;
}
ul.codex-soclist li:hover a {
  transition: all 0.5s ease;
  background-color: #6d64ac;
}
ul.codex-soclist li:hover a i {
  color: #ffffff;
}

/* loader css*/
.codex-loader {
  position: fixed;
  top: 0;
  left: 0;
  right: 17px;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  z-index: 99999;
  direction: ltr;
  display: none;
}

.codex-loader {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.codex-loader .linespinner {
  width: 10px;
  height: 30px;
  background: #6d64ac;
  position: relative;
  animation: linespinner 1s ease-in-out infinite;
  animation-delay: 0.4s;
  border-radius: 8px;
}
.codex-loader .linespinner:after, .codex-loader .linespinner:before {
  border-radius: 8px;
  content: "";
  position: absolute;
  width: 10px;
  height: 30px;
  background: #6d64ac;
  animation: linespinner 1s ease-in-out infinite;
}
.codex-loader .linespinner:before {
  right: 18px;
  animation-delay: 0.15s;
}
.codex-loader .linespinner:after {
  left: 18px;
  animation-delay: 0.6s;
}
@keyframes linespinner {
  0%, 100% {
    background-color: #6d64ac;
    box-shadow: 0 0 0 #6d64ac, 0 0 0 #6d64ac;
  }
  50% {
    background-color: #439acb;
    box-shadow: 0 -15px 0 #439acb, 0 15px 0 #439acb;
  }
}

/*tap to top*/
.scroll-top {
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  font-size: 22px;
  border: none;
  outline: none;
  background: #6d64ac;
  color: #ffffff;
  cursor: pointer;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  text-align: center;
  box-shadow: 0 3px 24px rgba(5, 23, 34, 0.1);
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}
.scroll-top.show {
  opacity: 1;
  visibility: visible;
}
.scroll-top:hover {
  color: #ffffff;
}

