/*/ Input-Rating*/
.pro-rating {
  direction: rtl;
}
.pro-rating input {
  display: none;
}
.pro-rating input:checked ~ .star:after, .pro-rating input:hover ~ .star:after {
  content: "\f005";
}
.pro-rating .star {
  display: inline-block;
  font-family: "FontAwesome";
  font-size: 16px;
  color: #FBB202;
  cursor: pointer;
  margin: 0 1px;
  color: #db7441;
}
.pro-rating .star:after {
  content: "\f006";
}
.pro-rating .star:hover ~ .star:after, .pro-rating .star:hover:after {
  content: "\f006";
  color: #db7441;
}

