.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  border-radius: 3px;
  overflow: hidden;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.switch input:checked + .switch-btn::before {
  transform: translateX(26px);
}
.switch input:disabled + .switch-btn {
  opacity: 0.5;
}
.switch .switch-btn {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: 0.4s;
}
.switch .switch-btn:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  transition: 0.4s;
  border-radius: 3px;
}
.switch.round {
  border-radius: 34px;
}
.switch.round .switch-btn:before {
  border-radius: 50%;
}
.switch.with-icon input:checked + .switch-btn::before {
  content: "\e64c";
}
.switch.with-icon .switch-btn::before {
  font-size: 12px;
  content: "\e646";
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "themify";
}

