.timeline {
  position: relative;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}
.timeline:after {
  content: "";
  position: absolute;
  width: 6px;
  background-color: var(--bs-bodybg-color);
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
}
.timeline.timeline-left {
  margin: unset;
}
.timeline.timeline-left:after {
  left: 25px;
}
.timeline.timeline-left .timeline-grid {
  padding-left: 80px;
}
.timeline.timeline-left .timeline-grid .icon-wrap {
  left: 0;
}
.timeline.timeline-left .timeline-grid .timeline-content {
  width: auto;
}
.timeline.timeline-left .timeline-grid .timeline-content:before {
  left: -10px;
  border-width: 10px 10px 10px 0;
  border-color: transparent var(--bs-bodybg-color) transparent transparent;
}

.timeline-grid {
  padding-top: 40px;
  position: relative;
}
.timeline-grid .icon-wrap {
  position: absolute;
  top: 45px;
  left: calc(50% - 25px);
  width: 50px;
  height: 50px;
  background-color: var(--bs-bodybg-color);
  border: 4px solid #439acb;
  border-radius: 50%;
  z-index: 1;
  overflow: hidden;
}
.timeline-grid .icon-wrap img {
  width: 100%;
  height: 100%;
}
.timeline-grid .timeline-content {
  padding: 30px;
  background-color: var(--bs-bodybg-color);
  position: relative;
  border-radius: 6px;
  width: 45%;
}
.timeline-grid .timeline-content::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  border: medium solid var(--bs-bodybg-color);
}
.timeline-grid .timeline-content h3 {
  margin-bottom: 10px;
}
.timeline-grid .timeline-content .timeline-date {
  font-weight: 500;
  color: #8392a5;
  text-transform: capitalize;
  margin-bottom: 10px;
}
.timeline-grid .timeline-content .timeline-date i {
  margin-right: 10px;
}
.timeline-grid .timeline-content .btn {
  margin-top: 20px;
}
.timeline-grid.left .timeline-content::before {
  right: -10px;
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent var(--bs-bodybg-color);
}
.timeline-grid.right .timeline-content {
  margin-left: auto;
}
.timeline-grid.right .timeline-content:before {
  left: -10px;
  border-width: 10px 10px 10px 0;
  border-color: transparent var(--bs-bodybg-color) transparent transparent;
}
.timeline-grid.right.icon-wrap {
  left: -16px;
}

@media screen and (max-width: 600px) {
  .timeline::after {
    left: 31px;
  }
  .timeline-grid {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }
  .timeline-grid::before {
    left: 60px;
    border: medium solid white;
    border-width: 10px 10px 10px 0;
    border-color: transparent white transparent transparent;
  }
  .left::after, .right::after {
    left: 15px;
  }
  .right {
    left: 0%;
  }
}
